import React from 'react';
import {injectStripe} from 'react-stripe-elements';

// import AddressSection from '../components/AddressSection';
import CardSection from '../CardSection/CardSection';

class CheckoutForm extends React.Component {

 constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this)
    this.state = {
    	amount: null,
    	lp: null,
    	beneficiary: null,
    }
  }

//   async handleSubmit(ev) {
//     // We don't want to let default form submission happen here, which would refresh the page.
//     ev.preventDefault();

//     // Within the context of `Elements`, this call to createToken knows which Element to
//     // tokenize, since there's only one in this group.
// 	  let {token} = await this.props.stripe.createToken({name: "Name"});
// 	  let response = await fetch("https://mysterious-caverns-61498.herokuapp.com/charge", {
// 	    method: "POST",
// 	    headers: {"Content-Type": "text/plain"},
// 	    body: token.id
// 	  });

// 	  if (response.ok) console.log("Purchase Complete!")


// }

handleAmountChange = (e) => {
	this.setState({amount: e.target.value});
}


handleLPChange = (e) => {
	this.setState({lp: e.target.value});
}

handleBeneficiaryChange = (e) => {
	this.setState({beneficiary: e.target.value});
}

  handleSubmit(ev) {
    // We don't want to let default form submission happen here, which would refresh the page.
    ev.preventDefault();

    // Within the context of `Elements`, this call to createToken knows which Element to
    // tokenize, since there's only one in this group.
	  this.props.stripe.createToken({name: "Name"}).then(({token, error}) => {
	  	if(error){
	  		console.log('err');
	  	} else {
	  		// fetch("https://mysterious-caverns-61498.herokuapp.com/charge", {
	  			fetch("http://localhost:3000/charge", {
		    method: "POST",
		    headers: {"Content-Type": "text/plain"},
		    body: JSON.stringify({stripeToken: token.id, amount: this.state.amount*100, lp: this.state.lp, beneficiary: this.state.beneficiary})
		  });
	  	}
	  });

	  // if (response.ok) console.log("Purchase Complete!")


}

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
      	<h1>A Picture of Possibility</h1>
      	<img src="https://static.wixstatic.com/media/25d4ca_8e5ffaa5d34d48158c02280e5fe16296~mv2_d_1280_1280_s_2.jpg/v1/fill/w_183,h_183,al_c,q_80,usm_0.66_1.00_0.01/Logo%20v3.webp" />
      	<div className="white-box">
	      	<p>A Picture of Possibility is an event to take a stand with The Torch Foundation, a non-profit organization committed to empowering teens with social, emotional, and leadership skills.</p>

			<p>We are raising $159,000 to empower Torch to cure the depression, addiction, bullying, violence, and school shootings that plague our young people.</p>


			<p>Take a stand for the lives of our youth --and our future-- by purchasing a ticket to the Torch Foundation benefit concert “A Picture of Possibility.”</p>
		</div>

        <label>Amount To Donate
        	<input placeholder="$25.00" value={this.state.amount} onChange={this.handleAmountChange}></input>
	    </label>
	    <label>To Which LP
        	<input placeholder="159" value={this.state.lp} onChange={this.handleLPChange}></input>
	    </label>
	    <label>To Who
			<input placeholder="John Doe" value={this.state.beneficiary} onChange={this.handleBeneficiaryChange}></input>
	    </label>
        <CardSection />
        <button>Donate</button>
      </form>
    );
  }
}

export default injectStripe(CheckoutForm);